/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-oleti-section {
  display: block; }

/* Structure --------------------------------- */
.wp-block .wp-block[data-type="oleti/section"]:not([data-align="full"]):not([data-align="wide"]):not([data-align="left"]):not([data-align="right"]) {
  margin-left: auto;
  margin-right: auto; }

/* Block: Section ------------------------------ */
.wp-block-oleti-section .wp-block[data-type="core/heading"]:first-child * {
  margin-top: 0; }

.wp-block[data-type="oleti/section"] > .editor-block-list__block-edit > div > .wp-block-oleti-section.has-background > .wp-block-oleti-section__inner-container > .editor-inner-blocks > .editor-block-list__layout > .wp-block[data-align="full"],
.wp-block[data-type="oleti/section"][data-align="full"] > .editor-block-list__block-edit > div > .wp-block-oleti-section.has-background > .wp-block-oleti-section__inner-container > .editor-inner-blocks > .editor-block-list__layout > .wp-block[data-align="full"] {
  margin-left: 0;
  width: 100%; }

.block-editor-block-list__layout .block-editor-block-list__block[data-type="oleti/section"].has-child-selected > .block-editor-block-list__block-edit > [data-block] > .wp-block-oleti-section > .wp-block-oleti-section__inner-container > .block-editor-inner-blocks,
.block-editor-block-list__layout .block-editor-block-list__block[data-type="oleti/section"].is-selected > .block-editor-block-list__block-edit > [data-block] > .wp-block-oleti-section > .wp-block-oleti-section__inner-container > .block-editor-inner-blocks {
  padding: 14px; }

.wp-block[data-type="oleti/section"] > .editor-block-list__block-edit > div > .wp-block-oleti-section.has-background > .wp-block-oleti-section__inner-container > .editor-inner-blocks {
  margin-top: -32px;
  margin-bottom: -32px; }

.block-editor-block-list__layout .block-editor-block-list__block.has-child-selected > .block-editor-block-list__block-edit > [data-block] > div > .wp-block-oleti-section__inner-container > .block-editor-inner-blocks > .block-editor-block-list__layout > .block-editor-block-list__block:not(.is-selected) > .block-editor-block-list__block-edit::before {
  border: 1px dashed rgba(123, 134, 162, 0.3); }
